<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="evaluationEdit">
        <el-card class="box-card">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-position="left" label-width="160px" class="g-edit-form">
                <el-form-item label="测评项目：" prop="name">
                    <el-input v-model="ruleForm.name" placeholder="请输入测评项目名称"></el-input>
                </el-form-item>
                <el-form-item label="选取试卷：" prop="region">
                    <el-select
                        v-model="ruleForm.name"
                        filterable
                        remote
                        reserve-keyword
                        placeholder="请输入试卷名称"
                        :remote-method="remoteMethod"
                        :loading="loading">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="及格率：" prop="desc">
                    <el-input placeholder="请输入内容" v-model="ruleForm.h">
                        <template slot="append">%</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="考试时长：" prop="desc">
                    <el-input placeholder="请输入内容" v-model="ruleForm.h">
                        <template slot="append">分钟</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="考试起止时间" prop="name">
                    <el-date-picker
                        v-model="ruleForm.j"
                        type="datetimerange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="是否开放成绩" prop="region">
                    <el-radio v-model="ruleForm.r" label="1">是</el-radio>
                    <el-radio v-model="ruleForm.r" label="2">否</el-radio>
                </el-form-item>
                <el-form-item label="是否开放答案" prop="region">
                    <el-radio v-model="ruleForm.r" label="1">是</el-radio>
                    <el-radio v-model="ruleForm.r" label="2">否</el-radio>
                </el-form-item>
                <el-form-item label="支持重复考试" prop="region">
                    <el-radio v-model="ruleForm.r" label="1">是</el-radio>
                    <el-radio v-model="ruleForm.r" label="2">否</el-radio>
                </el-form-item>
                <el-form-item label="试题顺序" prop="region">
                    <el-radio v-model="ruleForm.r" label="1">随机顺序</el-radio>
                    <el-radio v-model="ruleForm.r" label="2">固定顺序</el-radio>
                </el-form-item>
                <el-form-item label="考试须知" prop="region">
                    <el-input type="textarea" :rows="4" v-model="ruleForm.name" placeholder="请输入考试须知"></el-input>
                </el-form-item>


                <el-form-item>
                    <el-button type="primary" @click="">提交</el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
    import RichText from '@/components/RichText/RichText.vue'
    import SelectTags from '@/components/SelectTags/SelectTags.vue';
    export default {
        data() {
            return {
                isSearchAtomCourse:true,


                ruleForm:{},
                labelList:[],
                options:[],
                loading:true,
                rules: {
                    name: [
                        { required: true, message: '请输入活动名称', trigger: 'blur' },
                        { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                    ],
                    region: [
                        { required: true, message: '请选择活动区域', trigger: 'change' }
                    ],
                    date1: [
                        { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
                    ],
                    date2: [
                        { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
                    ],
                    type: [
                        { type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change' }
                    ],
                    resource: [
                        { required: true, message: '请选择活动资源', trigger: 'change' }
                    ],
                    desc: [
                        { required: true, message: '请填写活动形式', trigger: 'blur' }
                    ]
                }
            }
        },
        methods: {
            remoteMethod(query) {
                if (query !== '') {
                    this.loading = true;
                    setTimeout(() => {
                        this.loading = false;
                        this.options = this.rules.filter(item => {
                            return item.label.toLowerCase()
                                .indexOf(query.toLowerCase()) > -1;
                        });
                    }, 200);
                } else {
                    this.options = [];
                }
            }
        },
        created() {
        },
        components: {
            RichText,SelectTags
        }
    }
</script>
